const debounce = require('lodash.debounce');
const topNavItems = document.querySelectorAll('.nav__item');
const subNavs =  document.querySelectorAll('.subNav');
const hamburger =  document.querySelector('.hamburger');
const toggles = document.querySelectorAll('.subNav__toggle');

//Events
document.addEventListener("DOMContentLoaded", setSubNavWidth);
window.addEventListener("resize", debounce(setSubNavWidth));
hamburger.addEventListener("click", toggleMenu);

[...toggles].forEach(x => x.addEventListener("touchend", toggleSubNav));
[...topNavItems].forEach(x => x.addEventListener("mouseenter", showSubNav));
// [...topNavItems].forEach(x => x.addEventListener("touchstart", showSubNav));
[...topNavItems].forEach(x => x.addEventListener("mouseleave", hideSubNav));
[...subNavs].forEach(x => x.addEventListener("mouseenter", hoverParent));
[...subNavs].forEach(x => x.addEventListener("mouseleave", removeParentHover));

function setSubNavWidth(){
	console.log(isLargeViewport);
	const fontSize = getRemSize();
	const width = window.innerWidth
		|| document.documentElement.clientWidth
		|| document.body.clientWidth;
	const isLargeViewport = width > fontSize * 48;

	if(isLargeViewport){
		const topNav = document.getElementsByClassName('nav__list')[0];
		const subNavs = document.getElementsByClassName('subNav__list');
		const leftOffset = topNav.getBoundingClientRect().left;
		
		const rightOffset = width - topNav.getBoundingClientRect().right;

		[...subNavs].forEach(x => x.style.paddingLeft = leftOffset + "px");
		[...subNavs].forEach(x => x.style.paddingRight = rightOffset + "px");
	}
}

function getRemSize(){
	const body = document.querySelector("body");
	const fontSizeString = window.getComputedStyle(body).getPropertyValue("font-size");

	return parseFloat(fontSizeString);
}

function showSubNav(event){
	event.target.querySelector('.subNav').classList.add('subNav--active');
}

function hideSubNav(event){
	event.target.querySelector('.subNav').classList.remove('subNav--active');

}
function toggleSubNav(event){
	event.preventDefault();
	event.currentTarget.querySelector('.subNav__arrow').classList.toggle('subNav__arrow--active');
	event.currentTarget.parentNode.querySelector('.subNav').classList.toggle('subNav--active');
}

function hoverParent(event){
	// event.preventDefault();
	event.target.closest('.nav__item').classList.add('nav__item--active');
}

function removeParentHover(event){
	// event.preventDefault();
	event.target.closest('.nav__item').classList.remove('nav__item--active');
}

function toggleMenu(event){
	document.querySelector(".nav").classList.toggle('nav--open');
	hamburger.classList.toggle('is-active');
}